@import "../../scss/mixin.scss";

.content {
  &__card {
    border: 1px solid #f3f3f3;
    padding: 20px;
    width: 210px;
    height: 270px;
    border-radius: 40px;
    @include hover-card();
    @include flex-container();
    flex-direction: column;
    align-items: normal;
    h5 {
      font-weight: 400;
      font-size: 14px;
    }
  }

  &__sneakers {
    width: 133px;
    height: 112px;
  }

  &__favorite {
    position: absolute;
  }

  &__card_bottom {
    @include flex-container();
  }

  &__price {
    @include flex-container();
    flex-direction: column;
    align-items: start;

    span {
      font-size: 13px;
      opacity: 0.5;
      text-transform: uppercase;
    }

    b {
      font-size: 14px;
    }
  }
}
